/* password validation*/

.password-validation {
  direction: rtl;
  max-height: 0;
  overflow: hidden;
  text-align: right;
  transition: max-height 0.3s ease-out;
}
.password-validation .progress {
  height: 0.7rem;
  border-radius: 0.1rem;
}

.password-validation svg {
  margin-left: 5px;
}

.password-validation h4 {
  font-weight: 500;
}
.password-validation span {
  display: block;
  font-size: 0.9rem;
  padding: 1px 5px;
  font-weight: 500;
  transition: all 0.2s;
}

.password-validation span.valid svg {
  animation: lookAtMe 1s;
}
@keyframes lookAtMe {
  0% {
    transform: scale(1, 1);
  }

  50% {
    transform: scale(1.5, 1.5);
  }

  100% {
    transform: scale(1, 1);
  }
}
.password-validation p.comment {
  display: block;
  font-size: 0.8rem;
  padding: 5px;
}

.password-validation span.invalid {
  font-weight: 400;
  color: #ccc;
}

.password-validation span.valid {
  color: #27ae60;
}

.password-validation.show {
  padding: 15px 5px;
}

.form-group {
  margin-bottom: 10px;
  position: relative;
}
.form-group label {
  font-weight: 500;
}

input:focus,
textarea:focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

input.invalid,
input:focus.invalid,
textarea.invalid,
textarea:focus.invalid {
  border-color: #dc3545;
}

input:focus.invalid,
textarea:focus.invalid {
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

form.curve input[type="text"],
form.curve .form-control {
  outline: none;
  width: 100%;
  border-radius: 8px;
  border-width: 2px;
}
form.curve button {
  border-radius: 8px;
}

/* start popup svg text */
.alert-validate::before {
  content: attr(data-validate);
  position: absolute;
  direction: rtl;
  max-width: 70%;
  background-color: white;
  border: 1px solid #c80000;
  border-radius: 13px;
  padding: 4px 10px 4px 24px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  left: 8px;
  pointer-events: none;
  color: #c80000;
  font-size: 13px;
  line-height: 1.4;
  text-align: right;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  -moz-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.alert-validate:hover:before {
  visibility: visible;
  opacity: 1;
}
.alert-validate.ltr::before {
  padding: 4px 24px 4px 10px;
  right: 8px;
  left: unset;
}

@media (max-width: 992px) {
  .alert-validate::before {
    visibility: visible;
    opacity: 1;
  }
}

/* checkbox */
.checkbox-container label {
  font-size: 20px;
  cursor: pointer;
}
.checkbox-container label input[type="checkbox"].checkbox {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 1.1em;
  height: 1.1em;
  border-radius: 2px;
  background: #ddd;
  margin-left: 8px;
  vertical-align: middle;
  cursor: pointer;
  position: relative;
  outline: 0;
  -webkit-transition: content 0.2s ease-in-out, background 0.2s ease-in-out;
  transition: content 0.2s ease-in-out, background 0.2s ease-in-out;
}
.checkbox-container label input[type="checkbox"].checkbox:checked {
  background: #44d;
}
.checkbox-container label input[type="checkbox"].checkbox:checked::after {
  content: "\2713";
  left: 50%;
  top: 50%;
  font-size: 0;
  font-weight: bold;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  vertical-align: middle;
  position: absolute;
  -webkit-animation: checkAppear 0.4s ease-in-out forwards;
  animation: checkAppear 0.4s ease-in-out forwards;
}
.checkbox-container label input[type="checkbox"].checkbox::after {
  content: "";
  color: white;
  font-size: 100%;
}
.checkbox-container label input[type="checkbox"].toggle {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 3em;
  height: 1.5em;
  background: #ddd;
  vertical-align: middle;
  border-radius: 1.6em;
  position: relative;
  outline: 0;
  margin: 5px;
  margin-top: -1px;
  cursor: pointer;
  -webkit-transition: background 0.1s ease-in-out;
  transition: background 0.1s ease-in-out;
}
.checkbox-container label input[type="checkbox"].toggle::after {
  content: "";
  width: 1.5em;
  height: 1.5em;
  background: white;
  position: absolute;
  border-radius: 1.2em;
  -webkit-transform: scale(0.7);
  transform: scale(0.7);
  left: 0;
  -webkit-box-shadow: 0 1px rgba(0, 0, 0, 0.5);
  box-shadow: 0 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: left 0.1s ease-in-out;
  transition: left 0.1s ease-in-out;
}
.checkbox-container label input[type="checkbox"].toggle:checked {
  background: #27ae60;
}
.checkbox-container label input[type="checkbox"].toggle:checked::after {
  left: 1.5em;
}
.checkbox-container label input[type="radio"].radio {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 1.1em;
  height: 1.1em;
  background: #ddd;
  vertical-align: middle;
  border-radius: 1.2em;
  position: relative;
  outline: 0;
  margin: 5px;
  cursor: pointer;
}
.checkbox-container label input[type="radio"].radio::after {
  content: "";
  width: 1.1em;
  height: 1.1em;
  background: deepskyblue;
  position: absolute;
  border-radius: 0.9em;
  -webkit-box-shadow: 0 1px rgba(0, 0, 0, 0.5);
  box-shadow: 0 1px rgba(0, 0, 0, 0.5);
  -webkit-transform: scale(0);
  transform: scale(0);
  display: none;
  animation: radioAppear 0.4s ease-in-out reverse;
}
.checkbox-container label input[type="radio"].radio:checked::after {
  display: block;
  -webkit-animation: radioAppear 0.4s ease-in-out forwards;
  animation: radioAppear 0.4s ease-in-out forwards;
}
@-webkit-keyframes checkAppear {
  50% {
    font-size: 2em;
  }
  100% {
    font-size: 1.15em;
  }
}
@keyframes checkAppear {
  50% {
    font-size: 2em;
  }
  100% {
    font-size: 1.15em;
  }
}
@-webkit-keyframes radioAppear {
  50% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }
}
@keyframes radioAppear {
  50% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
  }
}
